<template>
  <div class="tag" v-if="show">
    <div class="tagHeader">
      <span class="title">태그</span>
      <span class="save" @click="backFunc"><i class="fa fa-angle-left"></i>연락처</span>
      <span class="edit" @click="backFunc">편집</span>
    </div>
    <div class="tagBody">
      <ul>
        <li v-for="tag in tagNameSort" @click="tagSelect(tag.id)">
          <i class="fa fa-minus-circle" @click="alertDeleteTag('tagDelete', tag.id, tag.name)"></i>
          {{ tag.name }}
          <i class="fa fa-edit"></i>
        </li>
        <li v-if="addTagTogle">
          <i class="fa fa-minus-circle" @click="cancelAddTagTogleFunc"></i>
          <input id="tag" type="text" placeholder="태그명을 입력하세요." v-model="tagName">
          <button class="addClass" @click="addTag">추가</button>
        </li>
        <li>
          <i class="fa fa-plus-circle" @click="addTagTogleFunc"></i>
          태그 추가
        </li>
      </ul>
    </div>
    <tag-contact :show="openTagContact" :tagId="tagId" @close="openTagContact = false"></tag-contact>
    <confirm-modal :show="openConfirmModal" :content="confirmContent" :tagName="deleteTagName" @onDelete="onDelete" @close="openConfirmModal = false"></confirm-modal>
  </div>
</template>

<script>
import tagContact from '../tagContact/TagContact'
import ConfirmModal from '../../utilities/confirmModal/ConfirmModal'
import ConfirmData from '../../utilities/confirmModal/ConfirmData.json'

export default {
  name: 'Tag',
  props: ['show'],
  data () {
    return {
      msg: 'Tag Page',
      tagData: [],
      openConfirmModal: false,
      confirmContent: {},
      deleteTagId: 0,
      deleteTagName: '',
      tagName: '',
      addTagTogle: false,
      tagId: 0,
      openTagContact: false,
    }
  },
  mounted () {
    this.getTag();
  },
  watch: {
  },
  computed: {
    tagNameSort () {
      return this.tagData.sort((a, b) => {
        return a.name < b.name ? -1 : a.name > b.name ? 1 : 0;
      });
    }
  },
  methods: {
    tagSelect (id) {
      console.log('tagId', id);
      this.tagId = id;
      this.openTagContact = true;
    },
    cancelAddTagTogleFunc () {
      this.tagName = '';
      this.addTagTogle = false;
    },
    addTagTogleFunc () {
      this.tagName = '';
      this.addTagTogle = true;
    },
    backFunc () {
      this.$emit('close');
    },
    getTag () {
      this.$http.get(`/tags/`, {
      }).then((result => {
          this.tagData = result.data;
          console.log('tag api 호출', this.tagData)
        }))
        .catch(error => {
          alert('에러가 발생했습니다.')
        })
    },
    addTag () {
      if (this.tagName !== "") {
        console.log('태그 추가')
        this.$http.post(`/tags/`, {
          name: this.tagName
        }).then((result => {
            console.log('태그 생성 성공')
            this.getTag();
            this.addTagTogle = false;
          }))
          .catch(error => {
            alert('에러가 발생했습니다.')
          })
      }
    },
    alertDeleteTag (name, id, tagName) {
      this.deleteTagId = id;
      this.deleteTagName = tagName;
      this.openConfirmModal = true;
      this.confirmContent = ConfirmData[name];
    },
    onDelete (isDelete) {
      if (isDelete) {
        this.$http.delete(`/tags/${this.deleteTagId}`, {
        }).then((result => {
          console.log('태그 삭제')

      this.getTag();
        }))
        .catch(error => {
          alert('에러가 발생했습니다.')
        })
      }
    },
  },
  components: {
    ConfirmModal,
    tagContact
  }
}
</script>

<style lang="scss">
    @import "Tag.scss"
</style>



